import { _decorator, Component, EventTarget } from 'cc';
import { ServerCommunicator } from './ServerCommunicator';
import { UserDataManager } from './UserDataManager';
import { TelegramWebApp } from './telegram-web';
import { ToastManager } from './ToastManager';
const { ccclass } = _decorator;

export interface UserSuitcaseItemInfo {
    id: number;
    user_id: string;
    suitcase_id: number;
    item_id: number;
    status: string;
}

export interface SuitcaseItemInfo {
    id: number;
    name: string;
    collection_duration: number;
    price: number;
    chance: number;
    suitcase_type_id: number;
}

export interface UserSuitcaseInfo {
    id: number;
    user_id: string;
    suitcase_type_id: number;
    is_complete: number;
}

export interface UserMiningSuitcaseItemInfo {
    id: number;
    data_start_mining_item: string;
    user_id: string;
    item_id: number;
    is_finished: number;
}

@ccclass('SuitcasesDataManager')
export class SuitcasesDataManager extends Component {
    private static _instance: SuitcasesDataManager;

    public static get instance(): SuitcasesDataManager {
        if (!this._instance) {
            console.error("SuitcasesDataManager instance is not initialized!");
        }
        return this._instance;
    }

    public static eventTarget: EventTarget = new EventTarget();

    currentAllItems: SuitcaseItemInfo[] = [];
    currentItems: UserSuitcaseItemInfo[] = [];
    currentMiningItems: UserMiningSuitcaseItemInfo[] = [];
    currentSuitcases: UserSuitcaseInfo[] = [];

    onLoad() {
        if (SuitcasesDataManager._instance) {
            console.warn("SuitcasesDataManager instance already exists!");
            this.destroy();
            return;
        }
        SuitcasesDataManager._instance = this;

        this.forceUpdateData();
    }

    public get isInited(): boolean {
        return this.currentAllItems.length > 0 && this.currentMiningItems.length > 0 && this.currentSuitcases.length > 0;
    }

    public async forceUpdateData() {
        while (!UserDataManager.instance.isInited) {
            await new Promise(resolve => setTimeout(resolve, 100));
        }
        
        this.setAllItemsFromServer();
        this.setCurrentUserSuitcaseItemsFromServer();
        this.setCurrentMiningUserSuitcaseItemsFromServer();
        this.setCurrentSuitcasesFromServer();
    }

    async setAllItemsFromServer() {
        let webinitdata = "";
        if (UserDataManager.instance.isDebug) {
            webinitdata = UserDataManager.instance.initDataForDebug;
        } else {
            const tgWebApp = TelegramWebApp.Instance;
            if (!tgWebApp.isInited) {
                const successInitTg = await tgWebApp.init();
                if (!successInitTg) {
                    return;
                }
            }
    
            webinitdata = tgWebApp.getTelegramInitData();
        }
    
        const communicator = ServerCommunicator.instance;
    
        communicator.sendGetRequest('get_all_items', { webinitdata })
            .then(response => {
                console.log('Get all items response:', response);
    
                this.currentAllItems = response as SuitcaseItemInfo[];

                SuitcasesDataManager.eventTarget.emit('allItemsUpdated');
            })
            .catch(error => {
                ToastManager.getInstance().showToast("Error with getting all items: " + error, "error")
            });
    }

    async setCurrentUserSuitcaseItemsFromServer() {
        let webinitdata = "";
        if (UserDataManager.instance.isDebug) {
            webinitdata = UserDataManager.instance.initDataForDebug;
        } else {
            const tgWebApp = TelegramWebApp.Instance;
            if (!tgWebApp.isInited) {
                const successInitTg = await tgWebApp.init();
                if (!successInitTg) {
                    return;
                }
            }
    
            webinitdata = tgWebApp.getTelegramInitData();
        }
    
        const communicator = ServerCommunicator.instance;
    
        communicator.sendGetRequest('get_user_suitcase_items', { webinitdata })
            .then(response => {
                console.log('Get user items response:', response);
    
                this.currentItems = response as UserSuitcaseItemInfo[];

                console.log('Cur user items:', this.currentItems);

                SuitcasesDataManager.eventTarget.emit('currentUserSuitcaseItemsUpdated');
            })
            .catch(error => {
                ToastManager.getInstance().showToast("Error with getting user items: " + error, "error")
            });
    }

    async setCurrentMiningUserSuitcaseItemsFromServer() {
        let webinitdata = "";
        if (UserDataManager.instance.isDebug) {
            webinitdata = UserDataManager.instance.initDataForDebug;
        } else {
            const tgWebApp = TelegramWebApp.Instance;
            if (!tgWebApp.isInited) {
                const successInitTg = await tgWebApp.init();
                if (!successInitTg) {
                    return;
                }
            }
    
            webinitdata = tgWebApp.getTelegramInitData();
        }
    
        const communicator = ServerCommunicator.instance;
    
        communicator.sendGetRequest('get_all_user_mining_items', { webinitdata })
            .then(response => {
                console.log('Get mining user items response:', response);
    
                this.currentMiningItems = response as UserMiningSuitcaseItemInfo[];

                SuitcasesDataManager.eventTarget.emit('currentMiningUserSuitcaseItemsUpdated');
            })
            .catch(error => {
                ToastManager.getInstance().showToast("Error with getting mining user items: " + error, "error")
            });
    }

    async setCurrentSuitcasesFromServer() {
        let webinitdata = "";
        if (UserDataManager.instance.isDebug) {
            webinitdata = UserDataManager.instance.initDataForDebug;
        } else {
            const tgWebApp = TelegramWebApp.Instance;
            if (!tgWebApp.isInited) {
                const successInitTg = await tgWebApp.init();
                if (!successInitTg) {
                    return;
                }
            }
    
            webinitdata = tgWebApp.getTelegramInitData();
        }
    
        const communicator = ServerCommunicator.instance;
    
        communicator.sendGetRequest('get_all_user_suitcases', { webinitdata })
            .then(response => {
                console.log('Get mining user suitcases response:', response);
    
                this.currentSuitcases = response as UserSuitcaseInfo[];

                SuitcasesDataManager.eventTarget.emit('currentSuitcasesUpdated');
            })
            .catch(error => {
                ToastManager.getInstance().showToast("Error with getting user suitcases: " + error, "error")
            });
    }

    public async getAllItems(forceUpdate: boolean = false): Promise<SuitcaseItemInfo[]> {
        if (this.currentAllItems.length <= 0 || forceUpdate) {
            await this.setAllItemsFromServer();
        }
        return this.currentAllItems;
    }

    public async getCurrentUserSuitcaseItems(forceUpdate: boolean = false): Promise<UserSuitcaseItemInfo[]> {
        if (this.currentItems.length <= 0 || forceUpdate) {
            await this.setCurrentUserSuitcaseItemsFromServer();
        }
        return this.currentItems;
    }

    public async getCurrentMiningUserSuitcaseItems(forceUpdate: boolean = false): Promise<UserMiningSuitcaseItemInfo[]> {
        if (this.currentMiningItems.length <= 0 || forceUpdate) {
            await this.setCurrentMiningUserSuitcaseItemsFromServer();
        }
        return this.currentMiningItems;
    }

    public async getCurrentSuitcaseIndex(forceUpdate: boolean = false): Promise<number> {
        if (this.currentSuitcases.length <= 0 || forceUpdate) {
            await this.setCurrentSuitcasesFromServer();
        }
        const incompleteSuitcase = this.currentSuitcases.find(suitcase => suitcase.is_complete === 0);
        return incompleteSuitcase ? incompleteSuitcase.suitcase_type_id - 1 : null;
    }

    public async getCurrentSuitcaseFilledCount(forceUpdate: boolean = false): Promise<number> {
        if (this.currentSuitcases.length <= 0 || forceUpdate) {
            await this.setCurrentSuitcasesFromServer();
        }
        const filledSuitcases = this.currentSuitcases.filter(suitcase => suitcase.is_complete === 1);
        return filledSuitcases.length;
    }    

    public async collectMinedItem(): Promise<boolean> { //TODO !!!!!!!!!!!!!!!!!!! SERVER COMMUNICATION
        try {
            const userId = UserDataManager.instance.getUserId();
            await ServerCommunicator.instance.sendGetRequest('collect_mined_item?user_id=' + userId);
            return true;
        } catch (error) {
            console.error('Error collecting mined item:', error);
            return false;
        }
    }
}
